html,
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  background-color: #f4f4f4;
  color: #3b3b3b;
  font-weight: 400;
  line-height: 1.25em;
}
@media screen and (max-width: 800px) {
  html,
  body {
    font-size: 19px;
  }
}
@media screen and (min-width: 800px) {
  html,
  body {
    font-size: 17px;
  }
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
div,
html,
body {
  margin: 0;
  padding: 0;
}
strong,
em {
  font-weight: 700;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  font-weight: 400;
  line-height: 1.2em;
}
h1 {
  margin: 2em 0 0.2em;
}
@media screen and (max-width: 800px) {
  h1 {
    font-size: 1.5em;
  }
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 2em;
  }
}
h1:first-of-type {
  margin-top: 0;
}
h2 {
  font-size: 1.5em;
  margin: 1.3em 0 0.3em;
}
h2.title:first-of-type {
  margin-top: 0;
}
h3,
h4,
h5,
h6 {
  font-size: 1.2em;
  margin: 1.5em 0 0.5em;
}
a {
  font-weight: 700;
}
a,
a:visited,
a:link {
  color: #5689e6;
  text-decoration: none;
}
a:active,
a:hover {
  color: #1c54ba;
  text-decoration: none;
}
a:focus {
  border-radius: 5px;
}
a::selection,
a::-moz-selection {
  color: #5689e6;
}
::selection,
::-moz-selection {
  background-color: #c5d7f6;
}
*:focus {
  box-shadow: 0 0 0 2px #5689e6;
  outline: none;
}
.container {
  width: 100%;
  max-width: 424px;
}
@media screen and (max-width: 800px) {
  .container {
    padding: 0 12px;
    margin: 12px auto;
  }
}
@media screen and (min-width: 800px) {
  .container {
    margin: 5vh auto;
  }
}
.cover {
  display: block;
  width: 100%;
  height: auto;
  max-width: 400px / 2;
  max-height: 400px / 2;
  margin: 0 auto 1em auto;
  border-radius: 5px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
}
.title {
  text-align: center;
}
.description {
  text-align: center;
}
.links {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
  gap: 12px;
}
.links .link {
  background-color: #5689e6;
  border-radius: 5px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media screen and (max-width: 800px) {
  .links .link {
    padding: 12px;
  }
}
@media screen and (min-width: 800px) {
  .links .link {
    padding: 6px 12px;
  }
}
.links .link,
.links .link:visited,
.links .link:active {
  color: #f4f4f4;
}
.links .link:hover {
  color: #f4f4f4;
  background-color: #1c54ba;
}
.links .link.spotify {
  background-color: #1db954;
}
.links .link.spotify:hover,
.links .link.spotify:focus {
  background-color: #168d40;
}
.links .link.i-tunes {
  background-color: #db48d8;
}
.links .link.i-tunes:hover,
.links .link.i-tunes:focus {
  background-color: #c927c5;
}
.links .link.apple-music {
  background-color: #4aa3f9;
}
.links .link.apple-music:hover,
.links .link.apple-music:focus {
  background-color: #198af7;
}
.links .link.bandcamp {
  background-color: #629aa9;
}
.links .link.bandcamp:hover,
.links .link.bandcamp:focus {
  background-color: #4c7e8c;
}
.links .link.sound-cloud {
  background-color: #ff8800;
}
.links .link.sound-cloud:hover,
.links .link.sound-cloud:focus {
  background-color: #e67a00;
}
.links .link.google-play-music {
  background-color: #f55a34;
}
.links .link.google-play-music:hover,
.links .link.google-play-music:focus {
  background-color: #ea370c;
}
.links .link.deezer {
  background-color: #ff0092;
}
.links .link.deezer:hover,
.links .link.deezer:focus {
  background-color: #cc0075;
}
.links .link.tidal {
  background-color: #555555;
}
.links .link.tidal:hover,
.links .link.tidal:focus {
  background-color: #3b3b3b;
}
